<template>
  <w-nav-links />
  <transition name="nav-right">
    <w-nav v-if="isNavShown" />
  </transition>
  <router-view v-slot="{ Component }">
    <transition name="book-flip" mode="out-in">
      <component :is="Component" />
    </transition>
  </router-view>
</template>

<script>
import WNav from "./components/WNav";
import WNavLinks from "./components/WNavLinks.vue";

export default {
  components: { WNav, WNavLinks },
  computed: {
    isNavShown() {
      return this.$store.state.isNavShown;
    },
    thePartner() {
      return this.$store.state.partner;
    },
    thisUser() {
      return this.$store.state.user;
    },
    socket() {
      return this.$store.state.socket;
    },
  },
  beforeMount() {
    // Socket.io event handelers :

    // 1) Home Handelers :

    // 1)1) get the number of connected users :

    this.socket.on("sendConnected", (peopleConnected) => {
      this.$store.commit("setConnectedUsers", peopleConnected);
    });

    // 1)2) set this user's id when the socket connects to server :

    this.socket.on("updateId", (id) => {
      this.$store.commit("updateId", id);
    });

    // 1)3) set this user's room when it's set from the server :

    this.socket.on("updateRoom", (roomId) => {
      this.$store.commit("updateRoom", roomId);
    });

    // 1)4) set the new partner when the user matches :

    this.socket.on("updatePartner", ({ user1, user2 }) => {
      if (user1.id !== this.thisUser.id) {
        [user1, user2] = [user2, user1];
      }
      this.$store.commit("updatePartner", user2);
      document.getElementById("chat-link").click();
    });
  },
  mounted() {
    // reset user and partner when the app before the app is first loaded
    this.$store.commit("defaultPartner");
    // go to login page if the user is not registered
    if (localStorage.getItem("thisUser")) {
      this.$store.commit(
        "setUser",
        JSON.parse(localStorage.getItem("thisUser"))
      );
    } else if (!localStorage.getItem("thisUser") && this.thisUser.name === "") {
      document.getElementById("login-link").click();
    }
  },
};
</script>

