import { createStore } from "vuex";
import { io } from "socket.io-client";

export default createStore({
  state: {
    whisper_version: "1.1.0",
    //  192.168.1.9:5000
    socket: io("https://m-whisper.herokuapp.com/" /*, { autoConnect: false }*/),
    isNavShown: false,
    connected: 0,
    user: {
      id: "",
      room: "",
      name: "",
      age: 0,
      matchEveryOneStatus: false,
      details: {
        gender: "",
        country: "",
      },
      preferences: {
        gender: "",
        country: "any",
      },
      interests: {
        sports: false,
        music: false,
        technology: false,
        business: false,
        travelling: false,
        memes: false,
        movies: {
          action: false,
          drama: false,
          comedy: false,
          horror: false,
          superhero: false,
        },
      },
    },
    partner: {
      id: "",
      name: "",
      age: 0,
      details: {
        gender: "",
        country: "",
        interests: [],
      },
      prefferences: {
        gender: "",
        country: "",
        interests: [],
      },
    },
  },
  mutations: {
    updateId: (state, id) => {
      state.user.id = id;
      state.user.room = "";
    },
    updateRoom: (state, room) => {
      state.user.room = room;
    },
    updatePartner: (state, partner) => {
      state.partner = partner;
    },
    setUser: (state, user) => {
      state.user = user;
    },
    defaultPartner(state) {
      state.partner = {
        id: "",
        name: "",
        age: "",
        details: {
          gender: "",
          country: "",
        },
      };
    },
    setUserPrefs: (state, newData) => {
      state.user.preferences = newData.userPrefs;
      state.user.interests = newData.userInterests;
      state.user.matchEveryOneStatus = newData.matchEveryOneStatus;
      let thisUserWithoutId = state.user;
      thisUserWithoutId.id = "";
      thisUserWithoutId.room = "";
      localStorage.setItem("thisUser", JSON.stringify(thisUserWithoutId));
    },
    setConnectedUsers: (state, connected) => {
      state.connected = connected;
    },
    toggleNav: (state) => {
      state.isNavShown === false
        ? (state.isNavShown = true)
        : (state.isNavShown = false);
    },
    increaseByOne: (state, counter) => {
      state[counter]++;
    },
  },
  actions: {},
  modules: {},
});
