<script>
export default {
  name: "WNav",
  methods: {
    closeNav() {
      this.$store.commit("toggleNav");
    },
    logout() {
      localStorage.removeItem("thisUser");
      document.getElementById("login-link").click();
    },
    changeTheme() {
      document.body.classList.toggle("dark-mode");
    },
  },
};
</script>

